import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { TabsPage } from './tabs.page';

// Url des différentes pages de l'application
const authentication = '../pages/authentication/authentication.module#AuthenticationPageModule';
const home = '../pages/home/home.module#HomePageModule';
const registration = '../pages/registration/registration.module#RegistrationPageModule';
const abonnement = '../pages/abonnement/abonnement.module#AbonnementPageModule';
const incident = '../pages/incident/incident.module#IncidentPageModule';
const service = '../pages/service/service.module#ServicePageModule';
const informations = '../pages/informations/informations.module#InformationsPageModule';

const financeManagement = '../pages/manager/finance-management/finance-management.module#FinanceManagementPageModule';
const logisticManagement = '../pages/manager/logistic-management/logistic-management.module#LogisticManagementPageModule';
const clientManagement = '../pages/manager/client-management/client-management.module#ClientManagementPageModule';
const employeManagement = '../pages/manager/employe-management/employe-management.module#EmployeManagementPageModule';

const subscribeManagement = '../pages/manager/subscribe-managemment/subscribe-managemment.module#SubscribeManagemmentPageModule';
const incidentManagement = '../pages/manager/incident-managemment/incident-managemment.module#IncidentManagemmentPageModule';
const serviceManagement = '../pages/manager/service-managemment/service-managemment.module#ServiceManagemmentPageModule';
const prospection = '../pages/commercial/prospection/prospection.module#ProspectionPageModule';
const configuration = '../pages/configuration/configuration.module#ConfigurationPageModule';
const intervention = '../pages/intervenant/intervention/intervention.module#InterventionPageModule';
const africleanHomePage =  '../pages/acueil/africlean-home/africlean-home.module#AfricleanHomePageModule';



const routes: Routes = [

          {
            path: '',
            component: TabsPage,
            children: [
              {
                path: '',
                redirectTo: 'accueil',
                pathMatch: 'full'
              },
              {
                path: 'home',
                loadChildren: home
              },
              {
                path: 'authentication',
                loadChildren: authentication
              },
              {
                path: 'registration',
                loadChildren: registration
              },
              {
                path: 'consult-abonnement',
                loadChildren: abonnement
              },
              {
                path: 'report-incident',
                loadChildren: incident
              },
              {
                path: 'consult-service',
                loadChildren: service
              },
              {
                path: 'consult-informations',
                loadChildren: informations
              },
              {
                path: 'consult-finance',
                loadChildren: financeManagement
              },
              {
                path: 'consult-logistic',
                loadChildren: logisticManagement
              },
              {
                path: 'consult-client',
                loadChildren: clientManagement
              },
              {
                path: 'consult-employe',
                loadChildren: employeManagement
              },

              {
                path: 'consult-subscribe-management',
                loadChildren: subscribeManagement
              },
              {
                path: 'consult-incident-management',
                loadChildren: incidentManagement
              },
              {
                path: 'consult-service-management',
                loadChildren: serviceManagement
              },

              {
                path: 'consult-prospection',
                loadChildren: prospection
              },
              {
                path: 'consult-configuration',
                loadChildren: configuration
              },
              {
                path: 'consult-intervention',
                loadChildren: intervention
              },
              {
                path: 'accueil',
                loadChildren: africleanHomePage
              }


            ]
          }



  // {
  //   path: '',
  //   children: [
  //     {
  //       path: '',
  //       children: [
  //         {
  //           path: '',
  //           loadChildren: home
  //         }
  //       ]
  //     },
  //     {
  //       path: 'home',
  //       children: [
  //         {
  //           path: '',
  //           loadChildren: home
  //         }
  //       ]
  //     },
  //     {
  //       path: 'authentication',
  //       children: [
  //         {
  //           path: '',
  //           loadChildren: authentication
  //         }
  //       ]
  //     }
  //   ]

  // }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class TabsPageRoutingModule { }
