import {Component, OnInit} from '@angular/core';
import {MenuController, NavController, AlertController} from '@ionic/angular';
import {AfricleanModalProvider} from "../../providers/africlean-modal/africlean-modal.provider";
import {AfricleanUseTermsComponent} from "../components/africlean-use-terms/africlean-use-terms.component";
import {AfricleanConfigProvider} from "../../providers/africlean-config/africlean-config.provider";
import {AfricleanLegalMentionComponent} from "../components/africlean-use-terms/africlean-legal-mention/africlean-legal-mention.component";


@Component({
    selector: 'app-tabs',
    templateUrl: './tabs.page.html',
    styleUrls: ['./tabs.page.scss'],
})
export class TabsPage implements OnInit {

    public africleanCopyrigth: any;
    isweb: boolean;

    constructor(private modal: AfricleanModalProvider, private config: AfricleanConfigProvider) {
    }

    ngOnInit(): void {
        const date = new Date();
        this.isweb = this.config.isWeb();
        this.africleanCopyrigth = '©Africlean ' + date.getFullYear();
        // this.africleanCopyrigth = '@Copyrigth Africlean ' + date.getFullYear() + '';

    }

    contactUs() {
    }

    async conditionsUse() {
        const props = {
            display: true
        };
        await this.modal.create(AfricleanUseTermsComponent, 'my-custom-modal-css', props);
    }

    async legalMention() {
        const props = {
            display: true
        };
        await this.modal.create(AfricleanLegalMentionComponent, 'my-custom-modal-css', props);
    }
}
